import React from 'react';
import $ from 'jquery';

window.jQuery = $;
window.$ = $;
global.jQuery = $;

const MultiLine = React.lazy(() => import('./Dashboard/MultiLine'));
const Maps = React.lazy(() => import('./Dashboard/Maps'));
const TSne = React.lazy(() => import('./Dashboard/TSne'));
const RaceChart = React.lazy(() => import('./Dashboard/RaceChart'));
const Gantt = React.lazy(() => import('./Dashboard/Gantt'));
const Intro = React.lazy(() => import('./Dashboard/Intro'));

const routes = [
    { path: `${window.appURL}/intro`, exact: true, name: 'Introduction', component: Intro },
    { path: `${window.appURL}/multi-lines`, exact: true, name: 'Multi-lines Visualization', component: MultiLine },
    { path: `${window.appURL}/maps`, exact: true, name: 'Map Visualization', component: Maps },
    { path: `${window.appURL}/t-sne`, exact: true, name: 'T-SNE Visualization', component: TSne },
    { path: `${window.appURL}/race-chart`, exact: true, name: 'Race Charts', component: RaceChart },
    { path: `${window.appURL}/gantt`, exact: true, name: 'Gantt Charts', component: Gantt },
];

export default routes;